@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@300;400;500&display=swap);
.app__gameList{max-width:1500px;padding-top:25px;margin:0 auto;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}.navbar{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;background-color:#171a21;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;width:100vw;box-shadow:0px 22px 38px rgba(0,0,0,0.25);padding:14px 30px}.navbar__logo{font-size:10px;width:150px}.navbar__search{width:996px;height:50px;padding-left:20px;background:#333;box-shadow:0px 0px 7px 5px rgba(6,7,8,0.25);border-radius:6px;border:0px;font-family:"IBM Plex Mono", monospace;font-weight:400;font-size:23px;letter-spacing:-0.005em;color:#ece6df}.navbar__search:focus{outline:none;background:#3b3b3b}.game{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:600px;margin:25px 0px 50px 0px;position:relative;-webkit-filter:drop-shadow(19px 10px 14px rgba(0,0,0,0.2));filter:drop-shadow(19px 10px 14px rgba(0,0,0,0.2))}.game__top{position:relative;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:200px}.game__top__img{width:100%;height:100%;width:-webkit-fill-available;object-fit:cover;object-position:0% 10%;-webkit-filter:drop-shadow(3px 0px 0px #ffc107);filter:drop-shadow(3px 0px 0px #ffc107);border-radius:0px 200px 0px 0px}.game__top__rating{position:absolute;top:12%;right:0px;background-color:#ffc107;color:black;border-radius:10px;padding:4px 10px}.game__top__name{position:absolute;bottom:50px}.game__top__platforms{position:absolute;bottom:5px}.game__bot{padding-top:15px;padding-left:15px;padding-right:40px;background-color:#18233d;box-shadow:3px 0px 0px #ffc107;border-radius:0px 0px 0px 6px;height:200px}.game__bot__text{font-size:1.15rem;letter-spacing:1px;word-spacing:5px;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:7;-webkit-box-orient:vertical}*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;background-color:#17172a;-webkit-font-smoothing:antialiased;font-family:"Roboto", sans-serif;color:white}

